import React from 'react'
import cn from 'classnames';

import walletIcon from '../../assets/images/icon/wallet.png';
import settingIcon from '../../assets/images/icon/setting.png';
import searchIcon from '../../assets/images/icon/search.png';
import favoriteActive from '../../assets/images/icon/favorite_active.png';
import favorite from '../../assets/images/icon/favorite.png';
import upIcon from '../../assets/images/icon/up.png';
import downIcon from '../../assets/images/icon/down.png';
import ethIcon from '../../assets/images/icon/ETH.png';
import switchIcon from '../../assets/images/icon/switch.png';
import dyorIcon from '../../assets/images/icon/dyor.png';
import arrowIcon from '../../assets/images/icon/arrow.png';
import discordIcon from '../../assets/images/icon/discord.png';
import twitterIcon from '../../assets/images/icon/twitter.png';
import telegramIcon from '../../assets/images/icon/telegram.png';
import copyIcon from '../../assets/images/icon/copy.png';
import searchRightIcon from '../../assets/images/icon/search-right.png';
import uniIcon from '../../assets/images/icon/uni.jpg';

const IconType: { [title: string]: string } = {
    wallet: walletIcon,
    setting: settingIcon,
    search: searchIcon,
    favorite: favorite,
    favoriteActive: favoriteActive,
    up: upIcon,
    down: downIcon,
    eth: ethIcon,
    switch: switchIcon,
    dyor: dyorIcon,
    arrow: arrowIcon,
    telegram: telegramIcon,
    discord: discordIcon,
    twitter: twitterIcon,
    copy: copyIcon,
    searchRight: searchRightIcon,
    uni: uniIcon
}

type IconProps = {
    symbol?: string;
    size?: number;
    radius?: boolean,
    iconSrc?: string,
}

const Icon: React.FC<IconProps> = ({ radius = false, size = 24, symbol, iconSrc}) => {
    if (!symbol && !IconType[symbol as string] && !iconSrc) {
        console.error(`Invalid icon symbol: ${symbol}`);
        return null
    }
    return (
        <img
            src={iconSrc || IconType[symbol as string]} alt=''
            className={cn(`dats-icon icon-${symbol}`)}
            style={{
                width: size + 'px',
                height: size + 'px',
                borderRadius: radius ? '50%' : 'unset'
            }}
        />
    )
}

export default Icon
