import React, { useState, useMemo } from "react";
import './index.scss'
import cn from "classnames";
import { useGlobal } from '../../../state/global/hooks'
import { KLineChart, NavTab } from '../../../components';
import SwapList from '../../Token/component/SwapList'
import TokenList from '../../Token/component/TokenList'
import { useList, useSetLiquidity } from '../../../state/list/hooks'
import SettingModal from "../../../components/Header/component/SettingModal";
import DyorModal from "../../../components/Header/component/DyorModal";
import Modal from "react-modal";
// import { Buy, Sell } from '../../Token/Trade'
import TradeHeader from '../../Token/component/TradeHeader'
import { Icon } from '../../../components'
import ReactTooltip from 'react-tooltip'


const MSwapList = () => {
  const { unitToken } = useGlobal()
  const { SwapLiquidityList } = useList()
  const setLiquidity = useSetLiquidity()

  return (
    <SwapList data={SwapLiquidityList} unitToken={unitToken} setLiquidity={setLiquidity} active={0} />
  )
}

type ButtonLiquidityProps = {
  setKey: Function
  setC: Function,
  currentTab: string
}

const ButtonLiquidity: React.FC<ButtonLiquidityProps> = ({ setKey, setC, currentTab }) => {

  return (
    <button className='tabliquidity' onClick={() => setKey('liquidity')}>
      Liquidity
      <i data-tip data-for="clickme" data-event="click" />
      <ReactTooltip
        clickable
        id='clickme'
        place='bottom'
        effect='solid'
        globalEventOff='click'
        backgroundColor="#181d23"
      >
        <div className='tips-buttons'>
          <a onClick={() => {
            setC('all')
          }}>All</a>
          <a onClick={() => {
            setC('add')
          }}>Add</a>
          <a onClick={() => {
            setC('rm')
          }}>Remove</a>
        </div>
      </ReactTooltip>
    </button>
  )
}


export default function MobileToken() {
  const [key, setKey] = useState<'swap' | 'liquidity' | 'trade'>('swap')
  const [tabAction, setTabAction] = useState<string | null>(null)
  const [condition, setC] = useState<string>('all')

  const TabFilter = (c: string) => {
    setC(c)
  }
  const TabAction = (key: string) => {
    setTabAction(key)
  }

  const [isOpen, ModalConnet] = useMemo(() => {
    if (!tabAction) return [false, null]
    switch (tabAction) {
      // case 'buy':
      //   return [true, <Buy />];
      // case 'sell':
      //   return [true, <Sell />];
      case 'dyor':
        return [true, <DyorModal />];
      case 'setting':
        return [true, <SettingModal />];
      default:
        return [false, null]
    }
  }, [tabAction])

  const SimpleBarContent = useMemo(() => {
    switch (key) {
      case 'swap':
        return <MSwapList />
      case 'liquidity':
        return <TokenList currentTab='liquidity' condition={condition} />;
      case 'trade':
        return <TokenList currentTab='trade' />
      default:
        return null
    }
  }, [key, condition])
  // console.log('condition', condition)

  return (
    <div className='p-mobile-token'>
      <div className='chart-wrapper'>
        {
          (key === 'trade' || key === 'liquidity') ? <TradeHeader type={key} /> : <KLineChart />
        }
      </div>
      <div className={cn('switch', { [key]: 1 })}>
        <button onClick={() => setKey('swap')} className='tabswap'>
          Swap
         <Icon symbol='searchRight' size={15} />
        </button>
        <ButtonLiquidity setC={TabFilter} setKey={setKey} currentTab={key} />
        <button onClick={() => setKey('trade')}>Trade</button>
      </div>
      {SimpleBarContent}
      <NavTab onAction={TabAction} />
      <Modal
        className='modal modal-glass'
        isOpen={isOpen}
        closeTimeoutMS={50}
        preventScroll={true}
        shouldFocusAfterRender={false}
        onRequestClose={() => {
          setTabAction(null)
        }}
        overlayClassName="overlayModal"
      >
        {ModalConnet}
      </Modal>
    </div>
  )
}
