import {axios} from "../utils";

export const getTokens = (cid:CHAIN) => {
  return axios.get(`/v1/tokens/${cid}`);
};

// export const getPairs = (token: string, cid: CHAIN) => {
//   return axios.get(`/v1/pairs/${token}/${cid}`);
// };

export const getPairs = (token: string) => {
  return axios.get(`/v1/pairs/${token}`);
};

export const getKlineData = (addr: string, typ: string = '1m', token: 0 | 1 = 0, starttime?: number) => {
  return axios.get(`/v1/pair/${addr}/kline/${token}/${typ}${starttime ? `?starttime=${starttime}` : ''}`);
};


