import React, { useMemo, useState, useEffect } from 'react'
import './index.scss';
import cn from 'classnames';
import SimpleBar from 'simplebar-react';
import Icon from '../../../../components/Icon'
import { useView } from '../../../../state/view/hooks'
import { useGlobal } from '../../../../state/global/hooks'
import { useList } from '../../../../state/list/hooks'
import { PoolTradeItemType } from '../../../../state/list/reducer'

type TokenListProps = {
    currentTab: string,
    condition?: string
}

type MSimpleTokenListProps = {
    data: PoolTradeItemType,
    isETH: boolean,
    animated: boolean,
    symbol: "ETH" | "$"
    currentTab: string,
}

const MSimpleTokenList: React.FC<MSimpleTokenListProps> = ({ data, isETH, symbol, animated, currentTab }) => {
    const { trxid, sign, from, to, eqTokenValue, time } = data
    return (
        <li key={trxid} className={cn({ [sign]: 1, 'fade-enter-done': animated })}>
            <div className='col-1'>
                {
                    currentTab === 'liquidity' ? <div className={cn('sign', { [sign]: 1 })} /> :
                        <Icon symbol={sign} size={16} />
                }
                <div className='amount'>
                    <div className='from-pair'>
                        <span className='num'>{from.amount}</span>
                        <span>{from.token}</span>
                    </div>
                    <div className='to-pair'>
                        <span className='num'>{to.amount}</span>
                        <span>{to.token}</span>
                    </div>
                </div>
            </div>
            <div className='col-2'>
                <div className='value'>
                    {!isETH && <i className='color3'>{symbol}</i>}
                    {eqTokenValue.amount}
                    {isETH && <span>{symbol}</span>}
                </div>
                <div className='emoji' />
            </div>
            <div className='col-3'>{`${time} m ago`}</div>
        </li>
    )
}

const TokenList: React.FC<TokenListProps> = ({ currentTab, condition }) => {
    const { isMobile } = useView()
    const { PoolList, TradeList } = useList()
    const { unitToken: { symbol, token } } = useGlobal()
    const isETH = token === "ETH"

    // const [listHeight, setHeight] = useState<number | null>(null)
    // useEffect(() => {
    //     if (!isMobile) {
    //         let switchHeight = document.getElementsByClassName('switch')[0].clientHeight;
    //         let ctradeHeight = document.getElementsByClassName('c-trade-header')[0].clientHeight;
    //         let sidebarHeight = document.getElementsByClassName('sidebar')[0].clientHeight
    //         setHeight(sidebarHeight - ctradeHeight - switchHeight - 100)
    //     }
    // }, [isMobile]);

    const [animated, setAnimated] = useState<boolean>(false);
    // useEffect(() => {
    //     const time = setTimeout(() => {
    //         setAnimated(!animated)
    //     }, 10000)
    //     return () => {
    //         clearTimeout(time)
    //     }
    // }, [animated])

    const data = useMemo(() => {
        if (currentTab === 'liquidity') {
            return condition ? PoolList.filter((item) => {
                return condition === 'all' || item.sign === condition
            }) : PoolList
        }
        return TradeList
    }, [currentTab, condition, PoolList, TradeList])

    // console.log('data', data)

    const TokenListElement = useMemo(() => {
        return data.map((item, i) => {
            let _animated = animated && i < 1
            return <MSimpleTokenList
                key={item.trxid}
                isETH={isETH}
                data={item}
                symbol={symbol}
                animated={_animated}
                currentTab={currentTab}
            />
        })
    }, [data, animated, isETH, currentTab])

    return (
        <div className='list'>
            <div className='list-header'>
                <span>Token Amount</span>
                <span>{`Token Value, ${symbol}`}</span>
                <span>Time</span>
            </div>
            {
                isMobile ? <div className='m-list__body'>
                    <ul>
                        {TokenListElement}
                    </ul>
                </div> : <SimpleBar style={{ height: '320px' }} forceVisible="y">
                    <ul>
                        {TokenListElement}
                    </ul>
                </SimpleBar>
            }
        </div>
    )
}
export default TokenList
