import React, { Fragment, useMemo } from 'react'
import { useWeb3React, UnsupportedChainIdError } from '@web3-react/core'
import { AbstractConnector } from '@web3-react/abstract-connector'
import { shortenAddress } from '../../../../utils'
import Identicon from '../../../Identicon'
import Icon from '../../../Icon'
import { injected } from '../../../../connectors'
import cn from 'classnames'

const ConnectWallet: React.FC = () => {
    const wallet = useWeb3React()
    // console.log('wallet', wallet)
    const { account, connector, error, activate } = wallet

    const tryAction = async (connector: AbstractConnector | undefined) => {
        connector && activate(connector, undefined, true).catch(error => {
            if (error instanceof UnsupportedChainIdError) {
                activate(connector)
            }
        })
    }

    const WalletDOM = useMemo(() => {
        if (account) {
            return (
                <div className='account-info' >
                    <span className='addr'>{shortenAddress(account)}</span>
                    <Identicon />
                </div >

            )
        }
        if (error) {
            return (
                <Fragment>
                    <Icon symbol="lighnting" size={16} />
                    <button className='error'>{error instanceof UnsupportedChainIdError ? 'Wrong Network' : 'Error'}</button>
                </Fragment>
            )
        }
        return (
            <button className='connect' onClick={() => { tryAction(injected) }}>
                Connect  a wallet
            </button>
        )

    }, [account, error, tryAction])

    return (
        <div className={cn('wallet', { Errormsg: error, Unconnect: !account && !error })}>
            {WalletDOM}
        </div>
    )
}

export default ConnectWallet