import './index.scss'
import { useState, useEffect, useRef, useCallback, useMemo } from 'react'
import { Icon } from '../../../../components'
import down from './down.svg'
import cn from 'classnames'
import { Currency, WETH, Token, ChainId, ETHER, JSBI } from '@uniswap/sdk'
import { BigNumber } from 'ethers'
import { Field } from '../../../../state/swap/actions'
import { useActiveWeb3React } from '../../../../hooks'
import { useCurrencyBalances, useCurrencyBalance } from '../../../../state/wallet/hooks'
import { useSwapActionHandlers } from '../../../../state/swap/hooks'

type Props = {
  isFrom: boolean,
  selectToken: boolean,
  field: Field,
  tokenName: string,
  value: string,
  currency: Currency
}

const percent = [25, 50, 75, 100];

let DAI = new Token(ChainId.ROPSTEN, '0xaD6D458402F60fD3Bd25163575031ACDce07538D', 18, 'DAI', 'Dai Stablecoin')

const Input: React.FC<Props> = ({
  isFrom = false, selectToken = false,
  field,
  tokenName,
  value,
  currency
}) => {
  const { account } = useActiveWeb3React()
  const [showDropDown, setShowDropDown] = useState(false);
  const selectRef = useRef<HTMLDivElement | null>(null)

  const tokenAmount = useCurrencyBalance(account ?? undefined, currency)

  const balance = useMemo(() => {
    if (!tokenAmount) return 0
    return tokenAmount.toSignificant(6)
  }, [tokenAmount])

  const { onUserInput } = useSwapActionHandlers()

  const closeSelect = useCallback(() => {
    selectToken && setShowDropDown(false)
  }, [selectToken])
  const stop = (event: Event) => {
    event.stopPropagation();
  }


  useEffect(() => {
    const ref = selectRef.current
    if (selectToken && ref) {
      document.addEventListener("click", closeSelect);
      ref.addEventListener("click", stop);
    }

    return () => {
      if (selectToken && ref) {
        document.removeEventListener('click', closeSelect)
        ref.removeEventListener('click', stop)
      }
    }
  }, [closeSelect, selectToken, showDropDown])

  const [quotient, setQ] = useState<number>(0)
  const setTypedValue = useCallback((Q: number) => {
    if (!balance) return
    let valued = Number(balance) * Q / 100
    onUserInput(field, valued.toFixed(4))
    setQ(Q)
  }, [balance, onUserInput])

  return (
    <div className={cn('c-input', { showDropDown: showDropDown })}>
      <p className='c-input-title'>
        <span>{isFrom ? 'From' : 'To'}</span>
        <span>{`Balance: ${balance || '-'}`}</span>
      </p>
      <div className='c-input-box'>
        <div className='c-input-wrapper'>
          <div className={cn('token-box', { select: selectToken })} onClick={selectToken ? () => { setShowDropDown(!showDropDown) } : undefined}>
            <Icon symbol='eth' />
            <span>{tokenName}</span>
            {selectToken && <img className='arrow' src={down} alt="" />}
          </div>
          <div className='input'>
            <input type="text" placeholder='0.0'
              value={value}
              onChange={(e) => {
                setQ(0)
                onUserInput(field, e.target.value)
              }}
            /></div>
        </div>
        {selectToken && showDropDown && (
          <div className='c-input-select' ref={selectRef}>
            <p className='title'>Select a token</p>
            <ul className='tokens'>
              <li><Icon symbol='eth' /><span>ETH</span></li>
              <li><Icon symbol='eth' /><span>ETH</span></li>
              <li><Icon symbol='eth' /><span>ETH</span></li>
            </ul>
            <input
              type="text"
              placeholder='Search Token Ticker'
            />
          </div>
        )}
      </div>

      <div className='c-input-btns'>
        {percent.map((p, i) => {
          return <button key={p}
            onClick={() => {
              setTypedValue(p)
            }}
            className={(quotient === p) ? 'active' : ''}>{p + '%'}</button>
        })}
      </div>
    </div>
  )
}

export const EstimateValue: React.FC<{ pair: [string, string] }> = ({ pair }) => {
  return (
    <div className={cn('c-input')}>
      <p className='c-input-title'>To</p>
      <div className='c-input-box'>
        <div className='c-input-wrapper'>
          <div className={cn('token-box')}>
            <Icon symbol='eth' />
            <span>{pair[1]}</span>
          </div>
          <div className='input'>
            <span className='estimate-value'>0.0</span>
          </div>
        </div>
      </div>
      <div className='c-input-rate'>
        <span>1 <i>{pair[0]}</i> = 1.08 <i>{pair[1]}</i> = 0.9995 <i>{pair[0]}</i></span>
      </div>
    </div>
  )
}

export default Input


