import './index.scss'
import React from "react";
import Icon from '../Icon'
import { useGlobal, useSwitchToken } from '../../state/global/hooks'
import cn from 'classnames'
type Props = {
  onAction?: (key: string) => void
}

const NavTab: React.FC<Props> = ({ onAction = () => { } }) => {
  const { unitToken } = useGlobal();
  const setUnitToken = useSwitchToken()
  return (
    <div className='c-navTab'>
      <div className='left'>
        <button className='trade-button' onClick={() => { onAction('buy') }}>Buy</button>
        <div className='div' />
        <button className='trade-button sell' onClick={() => { onAction('sell') }}>Sell</button>
      </div>
      <div className='right'>
        <button className={cn('switch-btn', { [unitToken.token]: 1 })}
          onClick={setUnitToken}
        >
          <span>USD</span>
          <span>ETH</span>
        </button>
        <button className='icon-btn' onClick={() => { onAction('dyor') }}>
          <Icon symbol="dyor" />
        </button>
        <button className='icon-btn' onClick={() => { onAction('setting') }}>
          <Icon symbol="setting" size={20} />
        </button>
      </div>
    </div>
  )
}

export default NavTab
