import { useUserActionHandlers, useUserState } from '../../../state/user/hooks'

let ETH = WETH[ChainId.GÃ–RLI]
let DAI = new Token(ChainId.ROPSTEN, '0xaD6D458402F60fD3Bd25163575031ACDce07538D', 18, 'DAI', 'Dai Stablecoin')

const SwapForm: React.FC<{
  title: string,
  _className: string,
  pair: [string, string]
  field: Field
}> = ({ title, _className, pair, field }) => {
  const { chainId, account, library, } = useActiveWeb3React()
  const isExactIn: boolean = field === Field.BUY

  const { [isExactIn ? Field.BUY : Field.SELL]: { typedValue } } = useSwapState()
  const [inputCurrency, outputCurrency] = useMemo(() => {
    return isExactIn ? [ETHER, DAI] : [DAI, ETHER]
  }, [field])
  const parsedAmount = tryParseAmount(typedValue, (isExactIn ? inputCurrency : outputCurrency) ?? undefined)

  const bestTradeExactIn = useTradeExactIn(isExactIn ? parsedAmount : undefined, outputCurrency ?? undefined)
  const bestTradeExactOut = useTradeExactOut(inputCurrency ?? undefined, !isExactIn ? parsedAmount : undefined)

  const trade = isExactIn ? bestTradeExactIn : bestTradeExactOut
  const { callback: swapCallback, error: swapCallbackError } = useSwapCallback(trade, 50, account ? account : null);

  return (
    <div className={cn("swap-input", { [_className]: 1 })}>
      <div className="title">
        {title}
      </div>
      <div className="from-input">
        <Input isFrom selectToken field={field}
          tokenName={pair[0]}
          value={typedValue}
          currency={inputCurrency}
        />
        <EstimateValue pair={pair} />
      </div>
      <div className='btn'>
        <button className={cn('trade-button', { [_className]: 1 })}>{title}</button>
      </div>
    </div>
  )
}

