import React, { useMemo, useState } from 'react'
import Icon from '../../../Icon'
import Modal from 'react-modal';
import SettingModal from '../SettingModal'
import DyorModal from '../DyorModal'
// import { useWeb3React } from '@web3-react/core'
// // import { shortenAddress } from '../../../../utils'
import ConnectWallet from './ConnectWallet'
import { useGlobal, useSwitchToken } from '../../../../state/global/hooks'
import './index.scss';
import cn from 'classnames'

Modal.setAppElement('#root')

const WalletButton: React.FC = () => {
    const [modalEle, setModlaEle] = useState<String | null>('')
    const { unitToken: { token, symbol } } = useGlobal();

    const setUnitToken = useSwitchToken()
    const onClose = () => {
        setModlaEle(null)
    }
    const open = useMemo(() => {
        return !!modalEle
    }, [modalEle])

    return (
        <>
            <div className="wallet-container">
                {/*<button
                    className={cn('switch-btn', { [token]: 1 })}
                    onClick={setUnitToken} >
                    <span>USD</span>
                    <span>ETH</span>
                </button>
                <button onClick={() => { setModlaEle('dyor') }}>
                    <Icon symbol='dyor' />
                </button>*/}
                <button onClick={() => { setModlaEle('setting') }}>
                    <Icon symbol="setting" />
                </button>
                <ConnectWallet />
            </div>
            <Modal
                className='modal modal-glass'
                isOpen={open}
                closeTimeoutMS={50}
                preventScroll
                shouldFocusAfterRender={false}
                onRequestClose={() => {
                    setModlaEle(null)
                }}
                overlayClassName="overlayModal"
            >
                {
                    modalEle === 'setting' && <SettingModal onClose={onClose} />
                }
                {
                    modalEle === 'dyor' && <DyorModal onClose={onClose} />
                }
            </Modal>
        </>
    )
}
export default WalletButton
