import React, { useState, useMemo } from 'react'
import './index.scss';
import cn from 'classnames';
import TokenList from '../component/TokenList'

import { poolData, tradingData } from '../data'
import TradeHeader from "../component/TradeHeader";
import {useGlobal} from "../../../state/global/hooks";

type itemType = {
    title: string,
    filter: boolean
}

const SidebarConfig: { [name: string]: itemType } = {
    liquidity: {
        title: "Pool Activity",
        filter: true
    },
    trade: {
        title: "Trading History",
        filter: false
    }
}

const TokenSideBar: React.FC = () => {
    const [key, setKey] = useState<'liquidity' | 'trade'>('liquidity')
    const [condition, setCondition] = useState<string>('all')

    const a = useMemo(() => {
        return SidebarConfig[key]
    }, [key])
    // console.log('a', a, 'key', key)
    const { title, filter } = a

    let _data = useMemo(() => {
        return key === 'liquidity' ? poolData : tradingData
    }, [key])


    _data = useMemo(() => {
        if (key === 'liquidity') {
            return condition === 'all' ? _data : _data.filter(item => item.sign === condition)
        }
        return _data
    }, [_data, condition, key])

    return (
        <div className='sidebar'>
            <div className={cn('switch', { [key]: 1 })}>
                <button onClick={() => setKey('liquidity')}>Liquidity</button>
                <button onClick={() => setKey('trade')}>Trading</button>
            </div>
            <TradeHeader type={key} />
            <div className="pool-activity">
                <div className='pool'>
                    <span className='title'>{title}</span>
                    {filter && <div className='filter'>
                        {([{ c: 'all', text: 'All' }, { c: 'add', text: 'Adds' }, { c: 'rm', text: 'Removes' }]).map(({ c, text }) => (
                            <button
                                className={cn('tab-button', { active: c === condition })}
                                onClick={() => setCondition(c)} key={c}
                            >{text}</button>
                        ))}
                    </div>}
                </div>
                <TokenList currentTab={key} />
            </div>
        </div>
    )
}
export default TokenSideBar
