import { useCallback } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { AppDispatch, AppState, } from '../index'
import { CurrentPairs, ListState, ListType, PoolTradeListType } from './reducer'
import { setLiqiudityList, setLiquidity, setPoolList, setTradingList, setSaved } from './actions'


export function useList(): ListState {
    return useSelector((state: AppState) => state.list)
}

export function useSetLiquidityList(): (data: ListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: ListType) => {
        dispatch(setLiqiudityList(data))
        dispatch(setLiquidity(data[0] ? {...data[0], index: 0} : null))
    }, [dispatch])
}
export function useSetLiquidity(): (data: CurrentPairs) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: CurrentPairs) => {
        dispatch(setLiquidity(data))
    }, [dispatch])
}
export function useSetPoolList(): (data: PoolTradeListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: PoolTradeListType) => {
        dispatch(setPoolList(data))
    }, [dispatch])
}

export function useSetTradeList(): (data: PoolTradeListType) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((data: PoolTradeListType) => {
        dispatch(setTradingList(data))
    }, [dispatch])
}
export function useFavtorite(): (index: number) => void {
    const dispatch = useDispatch<AppDispatch>()
    return useCallback((index: number) => {
        dispatch(setSaved(index))
    }, [dispatch])
}
